.statistics {
  margin: 0 auto;
  text-align: center;
  width: 300px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}
.title {
  font-size: 18px;
  font-weight: 700;
  height: 50px;
  background-color: white;
  margin: 0 auto;

  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}
.statList {
  margin: 0 auto;
  padding: 0;
  display: flex;
}
.item {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.item:nth-child(1) {
  background: #4fc5f5;
}

.item:nth-child(2) {
  background: #a53bf3;
}

.item:nth-child(3) {
  background: #e64c67;
}

.item:nth-child(4) {
  background: #20b8c5;
}

.item:nth-child(5) {
  background: #2d828a;
}
